<template>
  <div ref="pieChart" style="width: 100%; height: 100%;"></div>
</template>

<script lang="js">
  import echarts from 'echarts'

  export default {
    name: "PieChart",
    props: {
      option: {
        type: Object,
        default() {
          return {
            title: {
              text: ''
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}: {c} ({d}%)'
            },
            series: [
              {
                type: 'pie',
                radius: ['50%', '70%'],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    position: 'outer',
                    show: true
                  }
                },
                data: [
                  {value: 335, name: ''},
                  {value: 310, name: ''},
                  {value: 234, name: ''},
                  {value: 135, name: ''},
                  {value: 1548, name: ''}
                ]
              }
            ]
          }
        }
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        const Chart = echarts.init(this.$refs.pieChart);
        Chart.setOption(this.option)
      }
    }
  }
</script>

