<template>
  <el-dialog
          :title="title"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
    <el-form :model="form" :rules="rules" ref="basicInfoForm" :label-width="formLabelWidth">
      <el-form-item label="" prop="school">
        <el-input v-model="form.school" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="major">
        <el-input v-model="form.major" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="period">
        <el-date-picker
                v-model="form.period"
                type="daterange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width: 100%;">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="record">
        <el-select v-model="form.record" placeholder="" style="width: 100%;">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="scoreRank">
        <el-select v-model="form.scoreRank" placeholder="" style="width: 100%;">
          <el-option label=" 5%" value=" 5%"></el-option>
          <el-option label=" 10%" value=" 10%"></el-option>
          <el-option label=" 20%" value=" 20%"></el-option>
          <el-option label=" 30%" value=" 30%"></el-option>
          <el-option label=" 50%" value=" 50%"></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="studyType">
        <el-select v-model="form.studyType" placeholder="" style="width: 100%;">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button type="primary" size="small" @click="onsubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script lang="js">
  import {dateFormat} from '@/utils/dateFormat'

  let checkSchoolName = (rule, value, callback) => {
    let reg = /^[\u4e00-\u9fa5]{4,10}$/;
    if (!reg.test(value)){
      callback(new Error(''));
    }else {
      callback();
    }
  }
  let checkMajor = (rule, value, callback) => {
    let reg = /^[\u4e00-\u9fa5]{4,10}$/;
    if (!reg.test(value)){
      callback(new Error(''));
    }else {
      callback();
    }
  }

  export default {
    name: "EducationExperienceModal",
    data(){
      return {
        title: '',
        type: '',
        editItemIndex: '',
        dialogVisible: false,
        form: {
          school: '',
          major: '',
          period: '',
          record: '',
          scoreRank: '',
          studyType: ''
        },
        formLabelWidth: '80px',
        rules: {
          school: [
            { required: true, message: '', trigger: 'blur' },
            { validator: checkSchoolName, trigger: 'blur' }
          ],
          major: [
            { required: true, message: '', trigger: 'blur' },
            { validator: checkMajor, trigger: 'blur' }
          ],
          period: [
            { required: true, message: '', trigger: 'blur' },
          ],
          record: [
            { required: true, message: '', trigger: 'blur' },
          ],
          scoreRank: [
            { required: true, message: '', trigger: 'blur' }
          ],
          studyType: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      setDialogVisible(title, state, oldEduExp, type, index){
        this.title = title;
        this.dialogVisible = state;
        this.type = type;

        if (type === 'edit'){
          this.editItemIndex = index
          this.form.school = oldEduExp ? oldEduExp.school[1] : '';
          this.form.major = oldEduExp ? oldEduExp.major[1] : '';
          this.form.period = oldEduExp ? oldEduExp.period[1] : '';
          this.form.record = oldEduExp ? oldEduExp.record[1] : '';
          this.form.scoreRank = oldEduExp ? oldEduExp.scoreRank[1] : '';
          this.form.studyType = oldEduExp ? oldEduExp.studyType[1] : '';
        }else {
          this.form.school = '';
          this.form.major = '';
          this.form.period = '';
          this.form.record = '';
          this.form.scoreRank = '';
          this.form.studyType = '';
        }
      },
      handleClose(done) {
        // this.$confirm('').then(_ => {done();}).catch(_ => {});
        done()
      },
      onsubmit(){
        this.$refs.basicInfoForm.validate((valid) => {
          if (valid){
            let preTime = dateFormat(this.form.period[0]);
            let nextTime = dateFormat(this.form.period[1]);
            this.form.period = `${preTime} ~ ${nextTime}`;

            this.$emit('updateEduExp', this.form, this.type, this.editItemIndex);
            this.$message({message: '', type: 'success'});
            this.dialogVisible = false;
          }
        })
      }
    }
  }
</script>

