<template>
  <div class="basicInfo">
    <ResumeViewTitle title=""></ResumeViewTitle>
    <div class="infoItems">
      <div class="infoItem" v-for="(item, index) in basicInfo" :key="index">
        <div class="infoItemKey">{{item[0]}}</div>
        <div class="infoItemValue">{{item[1]}}</div>
      </div>
    </div>
    <el-button @click="editBasicInfo"></el-button>
    <el-button @click="goNextItem"></el-button>

    <BasicInfoModal ref="ResumePersonalInfoModal" @updateBasicInfo="updateBasicInfo"></BasicInfoModal>
  </div>
</template>

<script lang="ts">
  import ResumeViewTitle from "./ResumeViewTitle.vue";
  import BasicInfoModal from "./BasicInfoModal.vue";

  export default {
    name: "BasicInfo",
    components: {
      ResumeViewTitle,
      BasicInfoModal
    },
    data() {
      return {
        basicInfo: {
          name: ['', '**'],
          phone: ['', '188********'],
          mail: ['', '118806****@qq.com'],
          IDCard: ['', '******************'],
          currentState: ['', '2021'],
          expectedPosition: ['', '']
        }
      }
    },
    methods: {
      editBasicInfo() {
        this.$refs.ResumePersonalInfoModal.setDialogVisible('', true, this.basicInfo);
      },
      updateBasicInfo(form) {
        this.basicInfo.name = ['', form.name];
        this.basicInfo.phone = ['', form.phone];
        this.basicInfo.mail = ['', form.mail];
        this.basicInfo.IDCard = ['', form.IDCard];
        this.basicInfo.currentState = ['', form.currentState];
        this.basicInfo.expectedPosition = ['', form.expectedPosition];

        this.$EventBus.$emit('updateResumeBasicInfo', this.basicInfo);
      },
      goNextItem() {
        this.$emit('goNextItem', 'second');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .basicInfo {
    height: 750px;
    margin-right: 8px;

    > .infoItems {
      display: flex;
      flex-wrap: wrap;

      > .infoItem {
        width: 50%;
        margin-bottom: 16px;

        > .infoItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .infoItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
