<template>
  <div class="jobIntention">
    <ResumeViewTitle title=""></ResumeViewTitle>
    <el-alert
            title="~"
            type="warning"
            center
            show-icon
            :closable="false">
    </el-alert>

    <div class="jobItems">
      <div class="jobItem" v-for="(item, index) in jobIntention" :key="index">
        <div class="jobItemKey">{{item[0]}}</div>
        <div class="jobItemValue">{{item[1]}}</div>
      </div>
      <el-button @click="editJobIntention"></el-button>
      <el-button @click="finishResume"></el-button>
    </div>

    <JobIntentionModal ref="JobIntentionModal" @updateJobIntention="updateJobIntention"></JobIntentionModal>
  </div>
</template>

<script lang="js">
  import ResumeViewTitle from "@/views/personalResume/childComps/ResumeViewTitle"
  import JobIntentionModal from "@/views/personalResume/childComps/JobIntentionModal"

  export default {
    name: "JobIntention",
    components: {
      ResumeViewTitle,
      JobIntentionModal
    },
    data(){
      return {
        jobIntention: {
          expectedPosition: ['',''],
          expectedIndustry: ['',''],
          workCity: ['',''],
          salaryRank: ['','10-15k']
        }
      }
    },
    methods: {
      editJobIntention(){
        this.$refs.JobIntentionModal.setDialogVisible('', true, this.jobIntention);
      },
      updateJobIntention(form){
        this.jobIntention.expectedPosition = ['', form.expectedPosition[form.expectedPosition.length - 1]];
        this.jobIntention.expectedIndustry = ['', form.expectedIndustry];
        this.jobIntention.workCity = ['', form.workCity];
        this.jobIntention.salaryRank = ['', form.salaryRank];
      },
      finishResume(){
        this.$message({message: '...', type: 'warning'});
        setTimeout(() => {
          this.$message({message: '~', type: 'success'});
        }, 2000)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .jobIntention {
    height: 750px;
    margin-right: 8px;
    overflow-y: auto;

    > .jobItems {
      display: flex;
      flex-wrap: wrap;
      margin-top: 12px;
      margin-bottom: 12px;

      > .jobItem {
        width: 50%;
        margin-bottom: 16px;

        > .jobItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .jobItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
