<template>
  <div class="educationExp">
    <ResumeViewTitle title=""></ResumeViewTitle>
    <div class="eduItems" v-for="(eduExpItem,index) in eduExp">
      <div class="eduItem" v-for="(item, index) in eduExpItem" :key="index">
        <div class="eduItemKey">{{item[0]}}</div>
        <div class="eduItemValue">{{item[1]}}</div>
      </div>
      <el-button @click="editEduExp(index)"></el-button>
      <el-button @click="deleteEduExp(index)"></el-button>
      <el-button @click="addEduExp" v-if="index === eduExp.length-1"></el-button>
      <el-button @click="goNextItem" v-if="index === eduExp.length-1"></el-button>
    </div>
    <el-button @click="addEduExp" v-if="eduExp.length === 0"></el-button>
    <el-button @click="goNextItem" v-if="eduExp.length === 0"></el-button>

    <EducationExperienceModal ref="EducationExperienceModal" @updateEduExp="updateEduExp"></EducationExperienceModal>
  </div>
</template>

<script lang="js">
  import ResumeViewTitle from "./ResumeViewTitle.vue";
  import EducationExperienceModal from "./EducationExperienceModal.vue";

  export default {
    name: "EducationExperience",
    components: {
      ResumeViewTitle,
      EducationExperienceModal
    },
    data(){
      return {
        eduExp: [{
          school: ['',''],
          major: ['',''],
          period: ['','2017.09.07 ~ 2021.07.01'],
          record: ['',''],
          scoreRank: ['','50%'],
          studyType: ['', '']
        }]
      }
    },
    methods: {
      addEduExp(){
        this.$refs.EducationExperienceModal.setDialogVisible('', true, '', 'add');
      },
      editEduExp(index){
        this.$refs.EducationExperienceModal.setDialogVisible('', true, this.eduExp[index], 'edit', index);
      },
      deleteEduExp(deleteIndex){
        this.eduExp = this.eduExp.filter((item, index) => {
          return index !== deleteIndex;
        });

        this.$EventBus.$emit('updateResumeEduExp', this.eduExp);
      },
      updateEduExp(form, type, editItemIndex){
        if (type === 'add'){
          let newEduExpItem = {};
          newEduExpItem.school = ['',form.school];
          newEduExpItem.major = ['',form.major];
          newEduExpItem.period = ['',form.period];
          newEduExpItem.record = ['',form.record];
          newEduExpItem.scoreRank = ['',form.scoreRank];
          newEduExpItem.studyType = ['',form.studyType];
          this.eduExp.push(newEduExpItem);
        }else {
          this.eduExp[editItemIndex].school = ['',form.school];
          this.eduExp[editItemIndex].major = ['',form.major];
          this.eduExp[editItemIndex].period = ['',form.period];
          this.eduExp[editItemIndex].record = ['',form.record];
          this.eduExp[editItemIndex].scoreRank = ['',form.scoreRank];
          this.eduExp[editItemIndex].studyType = ['',form.studyType];
        }

        this.$EventBus.$emit('updateResumeEduExp', this.eduExp);
      },
      goNextItem(){
        this.$emit('goNextItem', 'third');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .educationExp {
    height: 750px;
    margin-right: 8px;
    overflow-y: auto;

    > .eduItems {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 12px;
      &:hover {
        background-color: #F9FBFF;
      }

      > .eduItem {
        width: 50%;
        margin-bottom: 16px;

        > .eduItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .eduItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
