<template>
  <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="38%"
      :before-close="handleClose">
    <el-form ref="userModalForm" :model="form" :rules="rules" :inline="true">
      <el-form-item label="" prop="name" :label-width="formLabelWidth">
        <el-input v-model="form.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender" :label-width="formLabelWidth">
        <el-input v-model="form.gender" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="grade" :label-width="formLabelWidth">
        <el-input v-model="form.grade" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="studentNo" :label-width="formLabelWidth">
        <el-input v-model="form.studentNo" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="major" :label-width="formLabelWidth">
        <el-input v-model="form.major" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address" :label-width="formLabelWidth">
        <el-input v-model="form.address" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="signCompany" :label-width="formLabelWidth">
        <el-input v-model="form.signCompany" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="signJob" :label-width="formLabelWidth">
        <el-input v-model="form.signJob" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="jobCity" :label-width="formLabelWidth">
        <el-input v-model="form.jobCity" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone" :label-width="formLabelWidth">
        <el-input v-model="form.phone" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancelSubmit"> </el-button>
      <el-button type="primary" size="small" @click="onSubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "StudentManageModal",
    data() {
      return {
        dialogVisible: false,
        formLabelWidth: '90px',
        addOrEdit: '',
        form: {
          id: 0,
          name: '',
          gender: '',
          grade: '',
          studentNo: '',
          major: '',
          address: '',
          signCompany: '',
          signJob: '',
          jobCity: '',
          phone: ''
        },
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          gender: [{required: true, message: '', trigger: 'blur'}],
          grade: [{required: true, message: '', trigger: 'blur'}],
          studentNo: [{required: true, message: '', trigger: 'blur'}],
          major: [{required: true, message: '', trigger: 'blur'}],
          address: [{required: true, message: '', trigger: 'blur'}],
          signCompany: [{required: true, message: '', trigger: 'blur'}],
          signJob: [{required: true, message: '', trigger: 'blur'}],
          jobCity: [{required: true, message: '', trigger: 'blur'}],
          phone: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    methods: {
      setDialogVisible(state, status, idOrStuInfo) {
        this.dialogVisible = state
        this.addOrEdit = status
        if (status === 'add') {
          this.form.id = idOrStuInfo + 1
        } else if (status === 'edit') {
          // form  resetFields() 
          this.$nextTick(() => {
            this.form = idOrStuInfo
          })
        }
      },
      handleClose(done) {
        // this.$confirm('').then(_ => {done();}).catch(_ => {})
        if (this.addOrEdit === 'edit') this.$refs.userModalForm.resetFields()
        done()
      },
      cancelSubmit() {
        this.dialogVisible = false
        if (this.addOrEdit === 'edit') this.$refs.userModalForm.resetFields()
      },
      onSubmit() {
        this.$refs.userModalForm.validate((valid) => {
          if (valid) {
            this.$emit('loadTableData', this.addOrEdit, JSON.parse(JSON.stringify(this.form)))
            if (this.addOrEdit === 'edit') this.$refs.userModalForm.resetFields()

            this.dialogVisible = false
            this.$message.success('~')
            this.$refs.userModalForm.resetFields()
          } else {
            this.$message.error('~')
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
 ::v-deep .el-dialog {

   .el-dialog__header {
     border-bottom: 1px solid #DCDFE6;
   }

   .el-dialog__body {
     padding: 20px 40px 10px;
   }

   .el-dialog__footer {
     border-top: 1px solid #DCDFE6;
   }
 }
</style>
