<template>
  <div class="langAbility">
    <ResumeViewTitle title=""></ResumeViewTitle>
    <div class="langItems" v-for="(langKindItem,index) in langKind">
      <div class="langItem" v-for="(item, index) in langKindItem" :key="index">
        <div class="langItemKey">{{item[0]}}</div>
        <div class="langItemValue">{{item[1]}}</div>
      </div>
      <el-button @click="editLangKind(index)"></el-button>
      <el-button @click="deleteLangKind(index)"></el-button>
      <el-button @click="addLangKind" v-if="index === langKind.length-1"></el-button>
      <el-button @click="goNextItem" v-if="index === langKind.length-1"></el-button>
    </div>
    <el-button @click="addLangKind" v-if="langKind.length === 0"></el-button>
    <el-button @click="goNextItem" v-if="langKind.length === 0"></el-button>

    <LanguageAbilityModal ref="LanguageAbilityModal" @updateLangKind="updateLangKind"></LanguageAbilityModal>
  </div>
</template>

<script lang="js">
  import ResumeViewTitle from "@/views/personalResume/childComps/ResumeViewTitle"
  import LanguageAbilityModal from "@/views/personalResume/childComps/LanguageAbilityModal"

  export default {
    name: "LanguageAbility",
    components: {
      ResumeViewTitle,
      LanguageAbilityModal
    },
    data(){
      return {
        langKind: [{
          lang: ['',''],
          proficient: ['','']
        }]
      }
    },
    methods: {
      addLangKind(){
        this.$refs.LanguageAbilityModal.setDialogVisible('', true, '', 'add');
      },
      editLangKind(index){
        this.$refs.LanguageAbilityModal.setDialogVisible('', true, this.langKind[index], 'edit', index);
      },
      deleteLangKind(deleteIndex){
        this.langKind = this.langKind.filter((item, index) => {
          return index !== deleteIndex;
        })

        this.$EventBus.$emit('updateResumeLangAbility', this.langKind);
      },
      updateLangKind(form, type, editItemIndex){
        if (type === 'add'){
          let newLangKindItem = {};
          newLangKindItem.lang = ['',form.lang];
          newLangKindItem.proficient = ['',form.proficient];
          this.langKind.push(newLangKindItem);
        }else {
          this.langKind[editItemIndex].lang = ['',form.lang];
          this.langKind[editItemIndex].proficient = ['',form.proficient];
        }

        this.$EventBus.$emit('updateResumeLangAbility', this.langKind);
      },
      goNextItem(){
        this.$emit('goNextItem', 'sixth');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .langAbility {
    height: 750px;
    margin-right: 8px;
    overflow-y: auto;

    > .langItems {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 12px;
      &:hover {
        background-color: #F9FBFF;
      }

      > .langItem {
        width: 50%;
        margin-bottom: 16px;

        > .langItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .langItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
