<template>
  <el-dialog
          :title="title"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
    <el-form :model="form" :rules="rules" ref="selfAssessForm" :label-width="formLabelWidth">
      <el-form-item label="" prop="selfAssess">
        <el-input
                type="textarea"
                :autosize="{ minRows: 4, maxRows: 7}"
                placeholder=""
                v-model="form.selfAssess">
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button type="primary" size="small" @click="onsubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script lang="js">
  export default {
    name: "SelfAssessModal",
    data() {
      return {
        title: '',
        dialogVisible: false,
        form: {
          selfAssess: ''
        },
        formLabelWidth: '80px',
        rules: {
          selfAssess: [
            { required: true, message: '', trigger: 'blur' },
          ]
        }
      }
    },
    methods: {
      setDialogVisible(title, state, oldSelfAssess){
        this.title = title;
        this.dialogVisible = state;

        this.form.selfAssess = oldSelfAssess;
      },
      handleClose(done) {
        // this.$confirm('').then(_ => {done();}).catch(_ => {});
        done()
      },
      onsubmit(){
        this.$refs.selfAssessForm.validate((valid) => {
          if (valid){
            this.$emit('updateSelfAssess', this.form);
            this.$message({message: '', type: 'success'});
            this.dialogVisible = false;
          }
        })
      }
    }
  }
</script>

