  <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="56%"
      :before-close="handleClose">
    <el-form ref="jobModalForm" :model="form" :rules="rules" :inline="true">
      <el-form-item label="" prop="companyName" :label-width="formLabelWidth">
        <el-input v-model="form.companyName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyCity" :label-width="formLabelWidth">
        <el-input v-model="form.companyCity" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyAddress" :label-width="formLabelWidth">
        <el-input v-model="form.companyAddress" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyScale" :label-width="formLabelWidth">
        <el-input v-model="form.companyScale" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyType" :label-width="formLabelWidth">
        <el-input v-model="form.companyType" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyWelfare" :label-width="formLabelWidth">
        <el-input v-model="form.companyWelfare" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="recruitJob" :label-width="formLabelWidth">
        <el-input v-model="form.recruitJob" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="recruitCount" :label-width="formLabelWidth">
        <el-input v-model="form.recruitCount" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="requiredDegree" :label-width="formLabelWidth">
        <el-input v-model="form.requiredDegree" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="requiredMajor" :label-width="formLabelWidth">
        <el-input v-model="form.requiredMajor" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="requiredExp" :label-width="formLabelWidth">
        <el-input v-model="form.requiredExp" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="salary" :label-width="formLabelWidth">
        <el-input v-model="form.salary" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="publishTime" :label-width="formLabelWidth">
        <el-input v-model="form.publishTime" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="label" :label-width="formLabelWidth">
        <el-input v-model="form.label" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="introduce" :label-width="formLabelWidth">
        <el-input v-model="form.introduce" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label=" Logo" prop="companyAvatar" :label-width="formLabelWidth">
        <el-input v-model="form.companyAvatar" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyPropaPic" :label-width="formLabelWidth">
        <el-input v-model="form.companyPropaPic" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="companyHomePage" :label-width="formLabelWidth">
        <el-input v-model="form.companyHomePage" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="jobDutyDetails" :label-width="formLabelWidth">
        <el-input v-model="form.jobDutyDetails" type="textarea" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="requiredSkillDesc" :label-width="formLabelWidth">
        <el-input v-model="form.requiredSkillDesc" type="textarea" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="abstract" :label-width="formLabelWidth">
        <el-input v-model="form.abstract" type="textarea" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancelSubmit"> </el-button>
      <el-button type="primary" size="small" @click="onSubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script lang="js">
export default {
  name: "JobsManageModal",
  data() {
    return {
      dialogVisible: false,
      formLabelWidth: '108px',
      addOrEdit: '',
      form: {
        id: 0,
        companyName: '',
        companyCity: '',
        companyAddress: '',
        companyScale: '',
        companyType: '',
        companyWelfare: '',
        recruitJob: '',
        recruitCount: '',
        requiredDegree: '',
        requiredExp: '',
        requiredMajor: '',
        jobDutyDetails: '',
        requiredSkillDesc: '',
        salary: '',
        publishTime: '',
        label: '',
        introduce: '',
        companyAvatar: '',
        companyPropaPic: '',
        companyHomePage: '',
        abstract: ''
      },
      rules: {
        companyName: [{required: true, message: '', trigger: 'blur'}],
        companyCity: [{required: true, message: '', trigger: 'blur'}],
        companyAddress: [{required: true, message: '', trigger: 'blur'}],
        companyScale: [{required: true, message: '', trigger: 'blur'}],
        companyType: [{required: true, message: '', trigger: 'blur'}],
        companyWelfare: [{required: true, message: '', trigger: 'blur'}],
        recruitJob: [{required: true, message: '', trigger: 'blur'}],
        recruitCount: [{required: true, message: '', trigger: 'blur'}],
        requiredDegree: [{required: true, message: '', trigger: 'blur'}],
        requiredExp: [{required: true, message: '', trigger: 'blur'}],
        requiredMajor: [{required: true, message: '', trigger: 'blur'}],
        salary: [{required: true, message: '', trigger: 'blur'}],
        publishTime: [{required: true, message: '', trigger: 'blur'}],
        label: [{required: true, message: '', trigger: 'blur'}],
        introduce: [{required: true, message: '', trigger: 'blur'}],
        companyAvatar: [{required: true, message: ' Logo', trigger: 'blur'}],
        companyPropaPic: [{required: true, message: '', trigger: 'blur'}],
        companyHomePage: [{required: true, message: '', trigger: 'blur'}],
        jobDutyDetails: [{required: true, message: '', trigger: 'blur'}],
        requiredSkillDesc: [{required: true, message: '', trigger: 'blur'}],
        abstract: [{required: true, message: '', trigger: 'blur'}]
      }
    }
  },
  methods: {
    setDialogVisible(state, status, idOrJobInfo) {
      this.dialogVisible = state
      this.addOrEdit = status
      if (status === 'add') {
        this.form.id = idOrJobInfo + 1
      } else if (status === 'edit') {
        // form  resetFields() 
        this.$nextTick(() => {
          this.form = idOrJobInfo
        })
      }
    },
    handleClose(done) {
      // this.$confirm('').then(_ => {done();}).catch(_ => {})
      if (this.addOrEdit === 'edit') this.$refs.jobModalForm.resetFields()
      done()
    },
    cancelSubmit() {
      this.dialogVisible = false
      if (this.addOrEdit === 'edit') this.$refs.jobModalForm.resetFields()
    },
    onSubmit() {
      this.$refs.jobModalForm.validate((valid) => {
        if (valid) {
          this.$emit('loadTableData', this.addOrEdit, JSON.parse(JSON.stringify(this.form)))
          if (this.addOrEdit === 'edit') this.$refs.jobModalForm.resetFields()

          this.dialogVisible = false
          this.$message.success('~')
          this.$refs.jobModalForm.resetFields()
        } else {
          this.$message.error('~')
        }
      })
    }
  }
}
</script>

