<template>
  <el-dialog
          :title="title"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
    <el-form :model="form" :rules="rules" ref="langAbilityForm" :label-width="formLabelWidth">
      <el-form-item label="" prop="lang">
        <el-select v-model="form.lang" placeholder="" style="width: 100%;">
          <el-option
                  v-for="item in langOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="proficient">
        <el-select v-model="form.proficient" placeholder="" style="width: 100%;">
          <el-option
                  v-for="item in proficientOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button type="primary" size="small" @click="onsubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script lang="js">
  export default {
    name: "LanguageAbilityModal",
    data(){
      return {
        title: '',
        type: '',
        editItemIndex: '',
        dialogVisible: false,
        form: {
          lang: '',
          proficient: ''
        },
        langOptions: [
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''}
        ],
        proficientOptions: [
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''}
        ],
        formLabelWidth: '80px',
        rules: {
          lang: [
            { required: true, message: '', trigger: 'blur' },
          ],
          proficient: [
            { required: true, message: '', trigger: 'blur' },
          ]
        }
      }
    },
    methods: {
      setDialogVisible(title, state, oldLangAbility, type, index){
        this.title = title;
        this.dialogVisible = state;
        this.type = type;

        if (type === 'edit'){
          this.editItemIndex = index
          this.form.lang = oldLangAbility ? oldLangAbility.lang[1] : '';
          this.form.proficient = oldLangAbility ? oldLangAbility.proficient[1] : '';
        }else {
          this.form.lang = '';
          this.form.proficient = '';
        }
      },
      handleClose(done) {
        // this.$confirm('').then(_ => {done();}).catch(_ => {});
        done()
      },
      onsubmit(){
        this.$refs.langAbilityForm.validate((valid) => {
          if (valid){
            this.$emit('updateLangKind', this.form, this.type, this.editItemIndex);
            this.$message({message: '', type: 'success'});
            this.dialogVisible = false;
          }
        })
      }
    }
  }
</script>

