<template>
  <div class="projectExp">
    <ResumeViewTitle title=""></ResumeViewTitle>

    <div class="projectItems" v-for="(projectExpItem,index) in projectExp">
      <div
              :class="index === 'describe' ? 'growWidth' : 'projectItem'"
              v-for="(item, index) in projectExpItem"
              :key="index">
        <div class="projectItemKey">{{item[0]}}</div>
        <div class="projectItemValue"><pre>{{item[1]}}</pre></div>
      </div>
      <el-button @click="editProjectExp(index)"></el-button>
      <el-button @click="deleteProjectExp(index)"></el-button>
      <el-button @click="addProjectExp" v-if="index === projectExp.length-1"></el-button>
      <el-button @click="goNextItem" v-if="index === projectExp.length-1"></el-button>
    </div>
    <el-button @click="addProjectExp" v-if="projectExp.length === 0"></el-button>
    <el-button @click="goNextItem" v-if="projectExp.length === 0"></el-button>

    <ProjectExperienceModal ref="ProjectExperienceModal" @updateProjectExp="updateProjectExp"></ProjectExperienceModal>
  </div>
</template>

<script lang="js">
  import ResumeViewTitle from "@/views/personalResume/childComps/ResumeViewTitle"
  import ProjectExperienceModal from "@/views/personalResume/childComps/ProjectExperienceModal"

  export default {
    name: "ProjectExperience",
    components: {
      ResumeViewTitle,
      ProjectExperienceModal
    },
    data(){
      return {
        projectExp: [{
          projectName: ['',''],
          projectRole: ['',''],
          period: ['','2018.09 ~ 2021.07'],
          projectURL: ['','https://github.com/yingjieweb'],
          describe: ['', 'bug']
        }]
      }
    },
    methods: {
      addProjectExp(){
        this.$refs.ProjectExperienceModal.setDialogVisible('', true, '', 'add');
      },
      editProjectExp(index){
        this.$refs.ProjectExperienceModal.setDialogVisible('', true, this.projectExp[index], 'edit', index);
      },
      deleteProjectExp(deleteIndex){
        this.projectExp = this.projectExp.filter((item, index) => {
          return index !== deleteIndex;
        })

        this.$EventBus.$emit('updateResumeProjectExp', this.projectExp);
      },
      updateProjectExp(form, type, editItemIndex){
        if (type === 'add'){
          let newProjectExpItem = {};
          newProjectExpItem.projectName = ['',form.projectName];
          newProjectExpItem.projectRole = ['',form.projectRole];
          newProjectExpItem.period = ['',form.period];
          newProjectExpItem.projectURL = ['',form.projectURL];
          newProjectExpItem.describe = ['',form.describe];
          this.projectExp.push(newProjectExpItem);
        }else {
          this.projectExp[editItemIndex].projectName = ['',form.projectName];
          this.projectExp[editItemIndex].projectRole = ['',form.projectRole];
          this.projectExp[editItemIndex].period = ['',form.period];
          this.projectExp[editItemIndex].projectURL = ['',form.projectURL];
          this.projectExp[editItemIndex].describe = ['',form.describe];
        }

        this.$EventBus.$emit('updateResumeProjectExp', this.projectExp);
      },
      goNextItem(){
        this.$emit('goNextItem', 'fifth');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .projectExp {
    height: 750px;
    margin-right: 8px;
    overflow-y: auto;

    > .projectItems {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 12px;
      &:hover {
        background-color: #F9FBFF;
      }

      > .projectItem {
        width: 50%;
        margin-bottom: 16px;

        > .projectItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .projectItemValue {
          margin-bottom: 4px;
        }
      }

      > .growWidth {
        width: 100%;
        margin-bottom: 16px;

        > .projectItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .projectItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
