<template>
  <el-dialog
          :title="title"
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
    <el-form :model="form" :rules="rules" ref="basicInfoForm" :label-width="formLabelWidth">
      <el-form-item label="" prop="expectedPosition">
        <el-cascader
          v-model="form.expectedPosition"
          :options="expectedPositionData"
          :props="{ expandTrigger: 'hover' }"
          :show-all-levels="false"
          style="width: 100%;"></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="expectedIndustry">
        <el-select v-model="form.expectedIndustry" placeholder="" style="width: 100%;">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="|" value="|"></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="workCity">
        <el-select v-model="form.workCity" placeholder="" style="width: 100%;">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="salaryRank">
        <el-select v-model="form.salaryRank" placeholder="" style="width: 100%;">
          <el-option label="5k" value="5k"></el-option>
          <el-option label="5-10k" value="5-10k"></el-option>
          <el-option label="10-15k" value="10-15k"></el-option>
          <el-option label="15-25k" value="15-25k"></el-option>
          <el-option label="25k" value="25k"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button type="primary" size="small" @click="onsubmit"> </el-button>
    </div>
  </el-dialog>
</template>

<script lang="js">
  import expectedPositionData from "@/database/expectedPositionData"

  export default {
    name: "JobIntentionModal",
    data(){
      return {
        title: '',
        dialogVisible: false,
        form: {
          expectedPosition: '',
          expectedIndustry: '',
          workCity: '',
          salaryRank: ''
        },
        formLabelWidth: '80px',
        rules: {
          expectedPosition: [
            { required: true, message: '', trigger: 'blur' }
          ],
          expectedIndustry: [
            { required: true, message: '', trigger: 'blur' }
          ],
          workCity: [
            { required: true, message: '', trigger: 'blur' }
          ],
          salaryRank: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        expectedPosition: '',
        expectedPositionData: expectedPositionData
      }
    },
    methods: {
      setDialogVisible(title, state, oldJobIntention){
        this.title = title;
        this.dialogVisible = state;

        this.form.expectedPosition = oldJobIntention.expectedPosition[1];
        this.form.expectedIndustry = oldJobIntention.expectedIndustry[1];
        this.form.workCity = oldJobIntention.workCity[1];
        this.form.salaryRank = oldJobIntention.salaryRank[1];
      },
      handleClose(done) {
        // this.$confirm('').then(_ => {done();}).catch(_ => {});
        done()
      },
      onsubmit(){
        this.$refs.basicInfoForm.validate((valid) => {
          if (valid){
            this.$emit('updateJobIntention', this.form);
            this.$message({message: '', type: 'success'});
            this.dialogVisible = false;
          }
        })
      }
    }
  }
</script>

