<template>
  <div class="internExp">
    <ResumeViewTitle title=""></ResumeViewTitle>
    <div class="hasIntern">
      <el-checkbox v-model="hasIntern"></el-checkbox>
      <div class="switchState" @click="switchState"></div>
    </div>
    <el-divider></el-divider>
    <div v-if="!hasIntern">
      <div class="internItems" v-for="(internExpItem,index) in internExp">
        <div
            :class="index === 'describe' ? 'growWidth' : 'internItem'"
            v-for="(item, index) in internExpItem"
            :key="index">
          <div class="internItemKey">{{item[0]}}</div>
          <div class="internItemValue"><pre>{{item[1]}}</pre></div>
        </div>
        <el-button @click="editInternExp(index)"></el-button>
        <el-button @click="deleteInternExp(index)"></el-button>
        <el-button @click="addInternExp" v-if="index === internExp.length-1"></el-button>
        <el-button @click="goNextItem" v-if="index === internExp.length-1"></el-button>
      </div>
      <el-button @click="addInternExp" v-if="internExp.length === 0"></el-button>
      <el-button @click="goNextItem" v-if="internExp.length === 0"></el-button>
    </div>

    <InternExperienceModal ref="InternExperienceModal" @updateInternExp="updateInternExp"></InternExperienceModal>
  </div>
</template>

<script lang="js">
  import ResumeViewTitle from "@/views/personalResume/childComps/ResumeViewTitle"
  import InternExperienceModal from "@/views/personalResume/childComps/InternExperienceModal"

  export default {
    name: "InternExperience",
    components: {
      ResumeViewTitle,
      InternExperienceModal
    },
    data(){
      return {
        hasIntern: false,
        internExp: [{
          company: ['', ''],
          period: ['', '2020.08.01~2020.05.01'],
          position: ['', ''],
          describe: ['', 'TableNetPytorchF10.9486F10.8933']
        }]
      }
    },
    watch: {
      hasIntern(){
        this.$EventBus.$emit('updateResumeInternExp', this.hasIntern, this.internExp);
      }
    },
    methods: {
      switchState(){
        this.hasIntern = !this.hasIntern;
      },
      addInternExp(){
        this.$refs.InternExperienceModal.setDialogVisible('', true, '', 'add');
      },
      editInternExp(index){
        this.$refs.InternExperienceModal.setDialogVisible('', true, this.internExp[index], 'edit', index);
      },
      deleteInternExp(deleteIndex){
        this.internExp = this.internExp.filter((item, index) => {
          return index !== deleteIndex;
        })

        this.$EventBus.$emit('updateResumeInternExp', this.hasIntern, this.internExp);
      },
      updateInternExp(form, type, editItemIndex){
        if (type === 'add'){
          let newInternExpItem = {};
          newInternExpItem.company = ['',form.company];
          newInternExpItem.period = ['',form.period];
          newInternExpItem.position = ['',form.position];
          newInternExpItem.describe = ['',form.describe];
          this.internExp.push(newInternExpItem);
        }else {
          this.internExp[editItemIndex].company = ['',form.company];
          this.internExp[editItemIndex].period = ['',form.period];
          this.internExp[editItemIndex].position = ['',form.position];
          this.internExp[editItemIndex].describe = ['',form.describe];
        }

        this.$EventBus.$emit('updateResumeInternExp', this.hasIntern, this.internExp);
      },
      goNextItem(){
        this.$emit('goNextItem', 'fourth');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .internExp {
    height: 750px;
    margin-right: 8px;
    overflow-y: auto;

    >.hasIntern {
      display: flex;
      padding: 8px 0;
      cursor: pointer;
      margin-bottom: -12px;

      > .switchState {
        flex-grow: 1;
      }
    }

    .internItems {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 12px;
      &:hover {
        background-color: #F9FBFF;
      }

      > .internItem {
        width: 50%;
        margin-bottom: 16px;

        > .internItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .internItemValue {
          margin-bottom: 4px;
        }
      }

      > .growWidth {
        width: 100%;
        margin-bottom: 16px;

        > .internItemKey {
          color: gray;
          margin-bottom: 8px;
        }

        > .internItemValue {
          margin-bottom: 4px;
        }
      }
    }
  }
</style>
